.about_image_container{
	justify-content:space-between;
	width: 43%;
	height: 45vh;
		.about_image1{
			overflow: hidden;
			// flex:1 1 65%;
			margin-right: 2.5%;
				img{
					transform:scale(1);
					display: block;
					// width: 100%;
					// height: 100%;
				}
		}
}
.inner_about{
	flex: 1 1 33%;
	flex-direction: column; 
	align-items:space-between;
		.about_image2{
			overflow: hidden;
			height: 46.5%;
			background-color: black;
			color: white;
				img{
					transform:scale(1);
					display: block;
					// width: 100%;
					// height: 100%;
				}
		}
		.about_image3{
			overflow: hidden;
			height: 46.5%;
			margin-top: auto;
				img{
					transform:scale(1);
					display: block;
				}
		}
}
.about_text_container{
	margin:5.5% 0 6.5% 0;
	justify-content:space-between;
	width: 100%;
}

.about_text_container>div{
	width: 50%;
}
.skills{
	flex-wrap: wrap;
	flex: 1 1 auto;
}
